.aside {
  grid-area: aside;
  background: #1b1813; 
  transition: all 0.3s ease;
  
   @media (max-width: 1100px) {
      position: absolute;
      top: 0;
      bottom: 0;
      right: -100%;
      z-index: 99999;
   }
}

.menu_open{
   right: 0;
}

.menu-level {
  display: grid;
  grid: repeat(3, auto) / 1fr;
  place-content: center;

		&__title {
      font-size: 22px;
      color: white;
      opacity: .4;
      font-weight: normal;
      margin: 0;
      padding: 23px 0 14px 16px;
		}

		&__levels {
         display: grid;
         grid: auto-flow 29px / 1fr;
      }
      
      &__link {
         padding: 0 12px 0 10px;
     }
     
     &__check-mark {
         position: relative;
         top: -1px;
      }

		&__num {
         opacity: .6; 
      }      

		&__selector {
         opacity: .6; 
		}

		&__button {
         display: grid;
         place-content: center;
         background: transparent;
         border: solid 2px rgba(255,255,255,.2);
         border-radius: 10px;
         width: calc(100% - 10px);
         padding: 5px 0;
         color: rgba(255,255,255,.4);
         @extend %font-base;
         cursor: pointer;
         place-self: center;
         &:hover{
            background: rgba(255,255,255,.05);
         }
		}
}

.check-mark{
   display: inline-block;
   width: 8px;
   height: 13px;
   opacity: 2;
   border: solid 3px white;
   border-width: 0 3px 3px 0;
   opacity: .2;
   transform: rotate(40deg);
   
   &__helped{
      opacity: .55;
      border: solid 3px #4a88bb;
      border-width: 0 3px 3px 0;
   }

   &__completed{
      opacity: .55;
      border: solid 3px #4cbb4a;
      border-width: 0 3px 3px 0;
   }
   
}

.link{
   display: grid;
   grid: auto / 22px 24px auto;
   cursor: pointer;
   color: #777;
   text-decoration: none;
   align-items: center;

   &__current{
      font-weight: bold;
      color: #AAA;
      background: rgba(255,255,255,.07);
   }

   &:hover{
      background: rgba(255,255,255,.05);
   }
}
