.editor {  
  grid-area: editor;
  min-width: 650px;
  display: grid;
  grid: auto / 1fr 1fr;
  grid-template-areas: 
  "css-header html-header"
  "css-editor html-viewer";
  border: solid 10px rgba(0,0,0,.35);
  border-radius: 4px;
  font-family: monospace;
  font-size: 14px;
  line-height: 150%;
  @media (max-width: 900px) {
    min-width: 100px;
    width: 100%;
    grid: auto auto/ 1fr;
    grid-template-areas: 
    "css-header"
    "css-editor"
    "html-header"
    "html-viewer";
  }

  &__title {
    text-align: left;
    padding: 10px 15px;
    background: rgba(0,0,0,.15);
    border-top: solid 1px rgba(255,255,255,.05);
    border-bottom: solid 1px rgba(255,255,255,.05);
    border-radius: 2px 2px 0 0;
    color: rgba(255,255,255,.6);

    &_css{
      grid-area: css-header; 
    }
    &_html{
      grid-area: html-header; 
    }
  }

  &__pane {   
    gap: 10px; 
  }
}
.css-editor {
  position: relative;
  grid-area: css-editor;
  display: grid;
  grid: auto / 35px 1fr auto;
  grid-template-areas: 
  "num input button"
  "num text text";
  place-content: center center;
  background: #efefef;
  color: #AAA;

  &__numbering {
    grid-area: num;
    color: #999;
    background: #DDD;
    padding: 10px 0 0 0;
  }

  &__input {
    grid-area: input;
    background: rgba(62,203,255,.3);
    color: #333;
    border: none;
    margin: 10px 0 0 0;
    animation: input .5s infinite;

    @keyframes input {
      50% { background:none ; }
    };
  }


  &__button {    
    position: absolute;
    top: -4px;
    right: -1px;
    grid-area: button;
    margin: 10px 10px 0 0;
    background: #ddd;
    border-radius: 2px;
    padding: 4px 8px 4px 8px;
    color: #666;
    border: solid 3px #999;
    border-width: 1px 1px 6px 1px;
    font-size: 9px;
    cursor: pointer;
    z-index: 99999;
  }
}

.numbering {
  justify-self: stretch;
  text-align: center;
}

.html-viewer {
  grid-area: html-viewer;
  display: grid;
  grid: auto / 35px 1fr;
  place-content: center center;
  border-right: solid 1px rgba(255,255,255,.03);
  border-bottom: solid 1px rgba(255,255,255,.03);
  color: #666;
  background: rgb(0,0,0);

  &__numbering {
    border-right: solid 1px #333;
  }  
}

.markup {

  &__viewer {
    display: flex;
    flex-direction: column;
    & div{
      padding: 0 0 0 10px;
    }
  }
}

.hovered {
  outline: 4px solid #666;
}


