.game {
  grid-area: table;

  &__wrapper {
    transform: translate3d(0,0,0);
    perspective: 400px;
    text-align: center;
    margin: 15px 0 50px 0;
    @media (max-width: 1100px) {
      transform: scale(0.8);
    }
    @media (max-width: 900px) {
      transform: scale(0.6);
    }
  }

  &__table {
    margin: 10px auto 0 auto;
  }
}
.table {
  opacity: 1;
  // width: 577px;
  min-height: 142px;
  transform: rotateX(20deg);
  transform-origin: bottom;
  z-index: 9999;
  position: relative;

  &__surface {
    box-shadow: 0px 40px 10px rgba(0,0,0,.2);
    background: #dd992d;
    background: -webkit-linear-gradient(#dd992d,#cd8c26);
    background: linear-gradient(#dd992d,#cd8c26);
    position: absolute;
    height: 100%;
    bottom: 0;
    // width: 100%;
  }

  &__items {
    margin: 0px auto 0px auto;
  }

  &__edge{
    width: 577px;
    margin: 0 auto 10px auto;
    background: #ac741c;
    height: 15px;
    transform: rotateX(-40deg);
    transform-origin: top;
    z-index: 2;
  }

  &__leg {
    width: 24px;
    height: 10px;
    background: #84570f;
    position: absolute;
    top: 15px;

    &_left{
      left: 20px;
    }
    &_right{
      right: 20px;
    }
  }
}

.items {
  display: inline-block;
  position: relative;
  padding: 15px 15px 22px 15px;
  transform-style: preserve-3d;
  outline: solid 1px transparent;
  min-height: 142px;
  white-space: nowrap;
  z-index: 999;
}

[data-hovered="true"] {
  box-shadow: 0 0 0 6px rgba(255,255,255,.4);
}

plate [data-hovered="true"] {
  box-shadow: 0 0 0 6px rgba(0,0,0,.3);
}
