bento {
  position: relative;
  display: inline-block;
  width: 100px;
  height: 100px;
  margin: 0 10px;
  box-shadow: 0px 8px 0px rgba(0,0,0,.1);
  background: brown;
  border: solid 3px #711c12;
  border-width: 20px 2px;
  @extend %transition;

  &:before{
    content: "";
    position: absolute;
    width: calc(100%);
    top: 0;
    left: 0;
    border: solid 10px rgba(0,0,0,.1);
    border-width: 10px 0 0 0px;
  }  

  plate & {
    height: 74px;
    width: 74px;
    position: absolute;
    top: -8px;
    left: 4px;
  }
}