apple {
  display: inline-block;
  position: relative;
  height: 60px;
  width: 60px;
  margin: 20px 10px;
  border-radius: 100px;
  border-style: solid;
  border-width: 2px 4px 15px 4px;
  border-color: #ab1212;
  background: #e41919;
  box-shadow: 0px 5px 0px rgba(0,0,0,.2);
  @extend %transition;  

  &:before{
    content: "";
    display: block;
    position: absolute;
    background: rgba(255,255,255,.3);
    height: 30%;
    width: 30%;
    top: 0px;
    left: calc(50% - 15%);
    border-radius: 100px;
  }
  
  .items > *  & {
    position: absolute;
    margin: 0;
    left: calc(50% - 30px);

    &.small {
      left: calc(50% - 15px);
      margin-left: 0;
      margin-right: 0;

      &:only-child{
        margin: 0;
        top: calc(50% - 15px);        
      }
    }
    &:last-child{
      top: calc(50% - 35px);
    }

    &:nth-last-child(2){
      top: -25px;
      z-index: 400;
    }

    &:nth-last-child(3){
      top: -65px;
      z-index: 500;
    }

  }


  &.small {
    height: 30px;
    width: 30px;
    border-width: 2px 4px 8px 4px;
    margin: 30px 10px;
  }

  plate bento & {
    position: absolute;
    left: calc(50% - 30px);
    top: -36px;
    margin: 0;
  }
}



