pickle {
  display: inline-block;
  height: 60px;
  width: 25px;
  position: relative;
  margin: 20px 10px;
  background: #179837;
  border-radius: 30px;
  border: solid 2px #025a18;
  border-width: 2px 2px 9px 2px;
  box-shadow: 0px 5px 0px rgba(0,0,0,.2);
  @extend %transition; 

  &.small{
    height: 40px;
    width: 18px;
    border-width: 1px 2px 6px 2px;
    margin: 30px 10px;
  }

  .items > * > & {
    &:last-child{
      position: absolute;
      top: calc(50% - 40px);
      left: calc(50% - 10px);
      margin: 0;
      z-index: 99999;
    }
    &.small:only-child{
      position: absolute;
      margin: 0;
      top: calc(50% - 22px);
      left: calc(50% - 8px);
      z-index: 99999;
    }

  }
}