plate {
  display: inline-block;
  position: relative;
  width: 100px;
  height: 100px;
  border-bottom: solid 5px #DDD;
  background: white;
  border-radius: 100px;
  box-shadow: 0px 8px 0px rgba(0,0,0,.1);
  margin: 0 10px;
  z-index: 99999;
  @extend %transition;
  
  &:before{
    content: "";
    display: block;
    position: absolute;
    left: calc(50% - 32px);
    top: calc(50% - 32px);
    height: 65px;
    width: 65px;
    border-radius: 60px;
    border-top: solid 6px #CCC;
    opacity: 0.4;
    background: rgba(255,255,255,1);
  }
}

#fancy {
  &:after{
    content: "";
    display: block;
    position: absolute;
    left: calc(50% - 47px);
    top: calc(50% - 44px);
    height: 68px;
    width: 74px;
    border-radius: 60px;
    border: solid 10px #009ff2;
    opacity: .4;
  }
}