.grid{
  height: 100vh;
  display: grid;
  grid: auto 1fr auto / 1fr 335px;
  place-content: center center;
  grid-template-areas: 
    "header header"
    "main aside"
    "footer aside";

    @media (max-width: 1100px) {
      grid: auto 1fr auto / 1fr 1fr;
      grid-template-areas: 
      "header header"
      "main main"
      "footer footer";
      height: 100%;
    }
}


.main__wrapper {
  display: grid;
  grid: auto auto auto auto / 1fr 1fr;
  grid-template-areas: 
  "description description"
  "button button"
  "table table"
  "editor editor";
  @media (max-width: 1100px) {
    grid: auto 1fr auto / 1fr 80px;
    grid-template-areas: 
    "description menu"
    "button button"
    "table table"
    "editor editor";
  }
}