.main{
  grid-area: main;
  justify-items: center;
  gap: 15px;
}

.task-description{
  grid-area: description;
  color: rgba(255,255,255,.3);
  font-weight: 400;
  font-size: 45px;
  text-align: center;
  padding: 0 20px;
}

.help-button{
  width: 200px;
  height: 50px;
  grid-area: button;
  display: grid;
  place-content: center;
  background: rgba(0,0,0,.2);
  color: rgba(255,255,255,.3);
  border-radius: 20px;
  text-decoration: none;
  @extend %font-base;
  cursor: pointer;
}

.menu-button{
  display: none;
  @media (max-width: 1100px) {
    display: block;
    grid-area: menu;
  }
}